html {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

body {
  display: flex;
  flex-direction: column;
  flex: auto;
  color: #333;
  background-image: url("./img/bgBody.jpg");
  padding-top: 64px; }

.header {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 49px;
  font-size: 15px;
  background-color: #008B8B;
  opacity: 0.9;
  z-index: 1000; }
  .header .container {
    height: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px 0 15px; }
    .header .container .logo {
      background-image: url("./img/whiteLogo.png");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      width: 100px;
      height: 100%; }
    .header .container .menu {
      display: flex;
      list-style: none;
      height: 100%; }
      .header .container .menu li a {
        color: #fff; }

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1; }
  .main .container {
    /* Portrait tablets and small desktops */
    /* Landscape phones and portrait tablets */ }
    .main .container .content {
      flex: 3;
      margin-right: 15px;
      margin-top: 49px;
      margin-bottom: 15px;
      min-height: 100px; }
    @media (min-width: 768px) and (max-width: 991px) {
      .main .container .content {
        flex: 1;
        margin-top: 98px;
        margin-right: 0;
        margin-bottom: 15px; } }
    @media (max-width: 767px) {
      .main .container .content {
        flex: 1;
        margin-right: 0;
        margin-top: 98px;
        margin-right: 0;
        margin-bottom: 15px; } }
  @media (max-width: 991px) {
    .main .container {
      flex-direction: column-reverse; } }

.footer {
  display: flex;
  flex-direction: column;
  height: 49px;
  border-top: 4px solid #008B8B; }
  .footer .container {
    height: 100%;
    line-height: 49px; }

#accord {
  margin: 0 auto;
  width: 400px; }

.task {
  margin-bottom: 10px; }

.linkTo a {
  display: block;
  padding: 10px;
  color: black;
  text-decoration: none;
  text-align: center;
  background: #f8f8f8;
  border: 1px solid gray; }

.linkTo :hover {
  color: red;
  background: #f2f2f2; }

.task.active .linkTo a {
  background: lightblue; }

.task:not(.active) .text {
  display: none; }

.task.active .text {
  display: inline; }

.task .text {
  border-top: none;
  padding: 10px;
  height: 500px; }

.group {
  text-align: center;
  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto; }

.group .menuGallery {
  margin: 0 auto;
  display: block;
  height: 15%; }

.menuGallery {
  margin-left: auto;
  margin-right: auto; }

.menuGallery a {
  width: 15%;
  height: 15%;
  margin: 0px 5px;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: black;
  text-decoration: none;
  border: 1px solid #c1b6b6;
  font-family: Arial;
  font-size: 12px;
  border-radius: 6px; }

.menuGallery a:hover {
  background: #f8f8f8;
  text-decoration: none;
  color: black; }

.menuGallery a.active {
  border-color: #d04638c2;
  background: #d6a33154;
  text-decoration: none;
  color: black; }

#parent.tabs {
  position: absolute;
  top: 0px;
  width: 65%;
  max-width: 300px; }

#pic {
  border-radius: 9px; }

.tabs {
  width: 100%;
  margin-left: auto;
  margin-right: auto; }

#dts {
  padding: 0px 5px;
  height: 10%; }

* {
  box-sizing: border-box; }

#prev, #next {
  display: inline-block;
  width: 25px;
  height: 25px;
  border: 1px  solid #d04638c2;
  color: grey; }

.dot:not(.active) {
  cursor: pointer;
  height: 10px;
  width: 10px;
  margin: 0 2px;
  background-color: #a02e2e;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  padding-top: 5px; }

.dot:hover {
  background-color: #717171; }

.dot.active {
  background-color: yellow;
  cursor: pointer;
  height: 10px;
  width: 10px;
  margin: 0 2px;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  padding-top: 10px; }

#prevTb, #nextTb {
  cursor: pointer;
  position: absolute;
  top: 35%;
  width: auto;
  margin-top: -22px;
  padding: 12px;
  color: white;
  font-size: 2em;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  background-color: transparent;
  border-color: transparent; }

.tabs #nextTb {
  right: 5%;
  border-radius: 6px; }

.tabs #prevTb {
  left: 5%;
  border-radius: 6px; }

/* On hover, add a black background color with a little bit see-through */
#prevTb:hover, #nextTb:hover {
  background-color: rgba(0, 0, 0, 0.8); }

.dot:hover {
  background-color: #717171; }

.num {
  color: #f2f2f2;
  font-size: 16px;
  padding: 8px 12px;
  position: absolute;
  top: 0; }

.tblGameX0 {
  border: 2px solid black;
  border-collapse: collapse;
  position: absolute;
  top: 30%;
  left: 35%;
  width: 25%;
  height: 25%; }

table {
  border: 1px solid black;
  border-collapse: collapse; }

td, th {
  text-align: center;
  border: 1px solid black; }

.none {
  pointer-events: none; }

.register {
  display: none; }

